/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.content-center {
  max-width: 80rem;
}

.align-left {
  display: flex;
  justify-content: flex-end;
}

.btn-yellow {
  text-transform: capitalize;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: 0.2rem solid transparent;
}
.btn-yellow:hover {
  cursor: pointer;
  border-color: #E08709;
  background-color: transparent;
  color: #E08709;
}

.btn-yellow-block {
  text-transform: capitalize;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: 0.2rem solid transparent;
}
.btn-yellow-block:hover {
  cursor: pointer;
  border-color: #E08709;
  background-color: transparent;
  color: #E08709;
}

.btn-purple {
  text-transform: capitalize;
  background-color: #42297d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: 0.2rem solid transparent;
}
.btn-purple:hover {
  cursor: pointer;
  border-color: #42297d;
  background-color: transparent;
  color: #42297d;
}

.dark-mode {
  background-color: #080808;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .property {
  background-color: #151515;
  border-color: #151515;
}
.dark-mode .price {
  color: #8c6fd0;
}
.dark-mode .details-icons img {
  filter: invert(50%);
}
.dark-mode .about-us-text blockquote {
  color: #FFFFFF;
}
.dark-mode .form label,
.dark-mode .form legend,
.dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #151515;
  color: #FFFFFF;
  border: 0.1rem solid #151515;
}

/*UI*/
.footer {
  padding: 2.5rem 0;
  background-color: #151515;
  margin-bottom: 0;
}
.footer .footer-content {
  padding-top: 2.5rem;
}
.footer .nav {
  transform: translateY(0%);
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #151515;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .header-content {
    min-height: 70rem;
  }
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.nav-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav-container {
    align-items: flex-end;
  }
}
.nav-container .btn-dark-mode {
  width: 3.5rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.nav-container .btn-dark-mode:hover {
  cursor: pointer;
  filter: invert(60%);
}
@media (min-width: 768px) {
  .nav-container .btn-dark-mode {
    margin-top: 0;
  }
}

.nav {
  transform: translateY(-300%);
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    opacity: 1;
    transform: translateY(0%);
  }
}
.nav a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-transform: capitalize;
}
.nav a:hover {
  color: #E08709;
}
@media (min-width: 768px) {
  .nav a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .nav a:last-of-type {
    margin-right: 0;
  }
}

/*Menú de hamburguesa*/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.show-nav {
  transform: translateY(0%);
  opacity: 1;
  height: auto;
  transition: transform 0.2s ease, opacity 0.8s linear, height 0.5s linear;
}

@media (min-width: 768px) {
  .about-us-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .properties {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.property {
  background-color: #f8f8f8;
  border: 0.1rem solid #e1e1e1;
  margin-bottom: 2rem;
}
.property .property-content {
  padding: 2rem;
}
.property h3, .property p {
  margin: 0;
}

.price {
  color: #42297d;
  font-weight: 700;
  font-size: 3rem;
}

.details-icons {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.details-icons li {
  flex: 1;
  display: flex;
}
.details-icons li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.details-icons li p {
  font-weight: 900;
}

.property-summary p {
  text-align: justify;
}

.no-webp .contact-image {
  background-image: url(../img/encuentra.jpg);
}

.webp .contact-image {
  background-image: url(../img/encuentra.webp);
}

.contact-image {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-image h2 {
  color: #FFFFFF;
  font-weight: 900;
  font-size: 4rem;
}
.contact-image p {
  color: #FFFFFF;
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .inferior-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-post {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-post {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-post:last-of-type {
  margin-bottom: 0;
}

.meta-info span {
  color: #E08709;
}

.text-post a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .text-post a {
    margin-top: 0;
  }
}
.text-post h4 {
  margin: 0;
  line-height: 1.4;
}
.text-post h4::after {
  content: "";
  margin-top: 1rem;
  display: block;
  max-width: 34rem;
  height: 0.5rem;
  background-color: #42297d;
}

.testimonial {
  background-color: #42297d;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #151515;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #151515;
}
.form label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: bloc;
  width: 100%;
  background-color: #FFFFFF;
  border: 0.1rem solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .contact-way {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/*internal*/
@media (min-width: 768px) {
  .about-us-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.about-us-text p {
  text-align: justify;
}
.about-us-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alert.error {
  background-color: #ba2b2b;
}
.alert.success {
  background-color: #26b355;
}